.carousel {
    background: #eee;
}

.carousel-cell {
    width: 100%;
    height: 450px;
    margin-right: 10px;
    background: $sewcase-black;
    border-radius: 5px;
    counter-increment: gallery-cell;

    @media (min-width: $md) {
        width: 50%;
    }
    @media (min-width: $lg) {
        height: 500px;
    }

    > img {
        width: 100%;
        object-fit: contain;
        margin-bottom: 16px;
    }
}

.orga-slider__content {
    padding-left: $spacing-md;
    padding-right: $spacing-md;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;

    .letter,
    p {
        color: $sewcase-white;
    }

    .letter {
        font-size: $font-md;
    }
}

.is-selected {
    > .orga-slider__content {
        opacity: 1;
        transition: opacity 0.3s ease-in-out;
    }
}
.flickity-prev-next-button {
    top: 25%;
    @media (min-width: $md) {
        top: 50%;
    }
}

#info span.letter {
    display: inline-block;
    animation: 5s shine backwards ease-in-out infinite;
}

@keyframes shine {
    90% {
        color: #fff;
    }
    100% {
        color: #ff0080 ;
    }
}
